<template>
  <div class="skuDetail">
    <div class="paramselect">
      <div class="infoCon">
        <div class="imgLeft">
          <img
            :src="
              detailInfo.head_imgs
                ? config.cdnbase + detailInfo.head_imgs[0]
                : ''
            "
            alt=""
          />
        </div>
        <div class="infoRight">
          <div class="promotion">
            <span v-if="detailInfo.promotion_name1">{{
              detailInfo.promotion_name1
            }}</span
            ><span v-if="detailInfo.promotion_name2">{{
              detailInfo.promotion_name2
            }}</span>
          </div>
          <div class="price">
            : <span>{{ detailInfo.money }}{{ detailInfo.price }}</span
            ><del>{{ detailInfo.money }}{{ detailInfo.oldprice }}</del>
          </div>
          <div class="product">: <span></span></div>
        </div>
      </div>
      <div class="specCon">
        <div class="option1 option">
          <div class="optName">{{ detailInfo.option1_name }}</div>
          <div class="option1_spec spec">
            <span
              v-for="(item, index) of spec.option1_list"
              :key="index"
              :class="{ active: prod.specname === item.name }"
              @click="option_name(item)"
            >
              {{ item.name }}
            </span>
          </div>
        </div>
        <div class="option2 option">
          <div
            class="optName"
            v-if="spec.option2_key_list && spec.option2_key_list.length > 0"
          >
            {{ detailInfo.option2_name }}
          </div>
          <div class="option2Spec spec">
            <span
              v-for="(item, index) of spec.option2_list"
              :key="index"
              :class="{
                active: prod.option1 === item.option1,
                disable: option2_disable(item)
              }"
              :disabled="option2_disable(item)"
              @click="option2_name(item)"
            >
              {{ item.option1 }}
            </span>
          </div>
        </div>
        <div class="option3 option">
          <div
            class="optName"
            v-if="spec.option3_key_list && spec.option3_key_list.length > 0"
          >
            {{ detailInfo.option2_name }}
          </div>
          <div class="option2Spec spec">
            <span
              v-for="(item, index) of spec.option3_list"
              :key="index"
              @click="option3_name(item)"
              :disabled="option3_disable(item)"
              :class="{
                active: prod.option2 === item.option2,
                disable: option3_disable(item)
              }"
              >{{ item.option2 }}</span
            >
          </div>
        </div>
      </div>
      <div class="numFormat">
        <div class="numTit"></div>
        <div class="numSelNumRow">
          <span
            class="clearNum symbol"
            :class="{ borderColor: prod.number === 1 }"
            @click="clearNum()"
            ><i class="icon iconfont buy-icon-jianhaob"></i></span
          ><input type="number" class="numNumber" v-model="prod.number" /><span
            class="addNum symbol"
            ><i class="icon iconfont buy-icon-jiahao" @click="addNum()"></i
          ></span>
        </div>
      </div>
      <div></div>
    </div>
    <div class="buyBtn">
      <div class="iconBtn" @click="colseSku()"><span></span></div>
      <div class="buyNow" @click="goBuy(1)"><span></span></div>
      <div class="addCart" @click="goBuy()"><span></span></div>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";
import { finish } from "../../lib/goodsSpec";
export default {
  name: "sku",
  data() {
    return {
      spec: {},
      prod: {
        specname: "",
        option1: "",
        option2: "",
        img: "",
        goods_id: "",
        price: "",
        tid: "",
        classgoods_id: "",
        sitename: "",
        checked: true,
        sku: "",
        number: 1
      }
    };
  },
  props: {
    detailInfo: {
      type: Object
    }
  },
  mounted() {
    this.getSpec();
    this.prod.sitename = this.detailInfo.sitename;
    this.prod.goods_id = this.detailInfo.goods_id;
    this.prod.tid = this.detailInfo.type
      ? this.detailInfo.type[0].fatherid
      : "";
    this.prod.classgoods_id = this.detailInfo.id;
  },
  methods: {
    ...mapMutations(["setCartList"]),
    colseSku() {
      this.$store.state.skuFlag = false;
    },
    getSpec() {
      this.spec = finish.finishGoods(this.detailInfo.GoodsSpecs);
    },
    //
    option_name(item) {
      if (this.prod.specname === item.name) {
        this.prod.specname = "";
        return;
      }
      this.prod.specname = item.name;
      if (
        this.spec.option2_list.length > 0 &&
        this.spec.option2_key_list.length === 1
      ) {
        if (this.spec.option2_list[0].specname === item.name) {
          this.prod.option1 = item.option1;
        }
      } else if (this.spec.option2_list.length === 0) {
        this.prod.sku = item.id;
        this.prod.img = item.img;
        this.prod.price = item.price;
      }
      this.option2_disable(item);
      this.option2_name(item);
    },
    //
    option2_disable(item) {
      for (let opt2 of this.spec.goods) {
        if (opt2.name === this.prod.specname && opt2.option1 === item.option1) {
          return false;
        }
      }
      return true;
    },
    //
    option2_name(item) {
      if (this.prod.option1 === item.option1) {
        this.prod.option1 = "";
        return;
      }
      let disableFlag = this.get_if_disable(1, item);
      if (!disableFlag) return false;
      if (
        this.spec.option3_list.length > 0 &&
        this.spec.option3_key_list.length === 1
      ) {
        if (this.spec.option2_list[0].specname === item.name) {
          this.prod.option2 = item.option2;
        }
      } else if (this.spec.option3_list.length === 0) {
        this.prod.sku = item.id;
        this.prod.img = item.img;
        this.prod.price = item.price;
      } else {
        let list = [];
        this.spec.goods.map(li => {
          if (
            li.name === this.prod.specname &&
            li.option1 === this.prod.option1
          ) {
            list.push(li);
          }
        });
        if (list.length === 1) {
          this.prod.option2 = list[0].option2;
        }
      }
      this.prod.option1 = item.option1;
      this.option3_name(item);
      this.option3_disable(item);
    },
    //
    option3_disable(item) {
      for (let opt3 of this.spec.goods) {
        if (
          opt3.name === this.prod.specname &&
          opt3.option1 === this.prod.option1 &&
          opt3.option2 === item.option2
        ) {
          return false;
        }
      }
      return true;
    },
    //
    option3_name(item) {
      if (this.prod.option2 === item.option2) {
        this.prod.option2 = "";
        return;
      }
      let disableFlag = this.get_if_disable(2, item);
      if (!disableFlag) return false;
      this.prod.option2 = item.option2;
      if (
        this.spec.option3_list.length > 0 &&
        this.spec.option3_list.length === 1
      ) {
        if (this.spec.option2_list[0].specname === item.name) {
          this.prod.option2 = item.option2;
        }
      } else if (this.spec.option3_list.length === 0) {
        this.prod.sku = item.id;
        this.prod.img = item.img;
        this.prod.price = item.price;
      }
      this.option2_disable(item);
    },
    get_if_disable(num, item) {
      for (let option of this.spec.goods) {
        if (this.prod.specname === option.name) {
          const where =
            num === 1 ? true : option["option1"] === this.prod["option1"];
          if (where && option["option" + num] === item["option" + num]) {
            return option;
          }
        }
      }
      return null;
    },
    showToast(txt) {
      this.toast = this.$createToast({
        txt: txt,
        type: "txt"
      });
      this.toast.show();
    },
    clearNum() {
      if (this.prod.number === 1) {
        this.showToast("");
        return;
      }
      this.prod.number--;
    },
    addNum() {
      this.prod.number++;
    },
    goBuy(flag) {
      if (!this.prod.sitename)
        return this.showToast("" + this.detailInfo.option1_name);
      for (let item of this.spec.goods) {
        if (item.name === this.prod.specname) {
          if (item.option1) {
            if (!this.prod.option1) {
              return this.showToast("" + this.detailInfo.option2_name);
            } else {
              if (item.option1 === this.prod.option1) {
                if (item.option2) {
                  if (!this.prod.option2) {
                    return this.showToast(
                      "" + this.detailInfo.option3_name
                    );
                  }
                }
              }
            }
          }
        }
      }
      if (this.cartList.length === 0) {
        //
        this.cartList.push(this.prod);
      } else {
        //0
        let prod = this.prod;
        for (let n = 0; n < this.cartList.length; n++) {
          if (
            this.cartList[n].goods_id === this.prod.goods_id &&
            this.cartList[n].sku === this.prod.sku
          ) {
            if (
              this.cartList[n].specname === this.prod.specname &&
              this.cartList[n].option1 === this.prod.option1 &&
              this.cartList[n].option2 === this.prod.option2
            ) {
              this.cartList[n].number = this.cartList[n].number + prod.number;
              break;
            } else {
              if (n === this.cartList.length - 1) {
                //
                this.cartList.push(prod);
              }
              break;
            }
          } else {
            if (n === this.cartList.length - 1) {
              //
              this.cartList.push(prod);
              break;
            }
          }
        }
        this.prod.number = 1;
      }
      this.setCartList(this.cartList);
      console.log(this.cartList);
      // if (flag) {
      // } else {
      // }
    }
  },
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config", "cartList"])
  },
  watch: {}
};
</script>

<style scoped></style>
