<script>
import { detail } from "../lib/goods";
import { mapState } from "vuex";
import countDown from "../components/detail/countDown";
import sku from "../components/detail/sku";

export default {
  name: "Detail",
  data() {
    return {
      detailInfo: {}, //
      swiperIndex: "", //
      imgLength: "",
      fathername: "",
      evaluateList: [],
      evaluateLength: "",
      startvalue: 5,
      serviceFlag: false,
      commentList: [
        {
          name: "****",
          body: "",
          head_img:
            "https://yanxuan.nosdn.127.net/8e4c1f403a88a156b8c80e2fb29249ba",
          body_img:
            "https://yanxuan.nosdn.127.net/cfdfc4ddf3268168d22a08aafdf99562.jpg;https://yanxuan.nosdn.127.net/9a1b56d484069a8c94a655c6d3d71bbd.jpg",
          star: 5,
          updated_at: "2019-06-24"
        }
      ],
      cartList: []
    };
  },
  created() {
    this.evaluateLength = this.commentList.length;
    this.$store.state.skuFlag = false;
  },
  mounted() {
    this.getDetailInfo();
  },
  methods: {
    async getDetailInfo() {
      let detail_info = await detail.get_detail_info(this);
      if (!detail_info.Error) {
        this.detailInfo = detail_info;
        this.imgLength = this.detailInfo.head_imgs
          ? this.detailInfo.head_imgs.length
          : 0;
        this.fathername = this.detailInfo.type[0].fathername;
        // this.getDetailComm();
      }
    },
    // async getDetailComm() {
    //   let evaluateList = await detail.get_detail_comm(this.detailInfo.goods_id);
    //   if (!evaluateList.Error) {
    //     this.evaluateList = evaluateList.list;
    //     this.evaluateLength = this.evaluateList.length;
    //     console.log(this.evaluateList);
    //   }
    // },
    formatDate(date) {
      let newdate = new Date(date);
      let y = newdate.getFullYear();
      let m = newdate.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = newdate.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    },
    changePage(current) {
      this.swiperIndex = current;
    },
    clickHandler(item, index) {
      console.log(item, index);
    },
    buyBtn() {
      this.$store.state.skuFlag = true;
    }
  },
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"]),
    //
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  },
  components: {
    countDown,
    sku
  }
};
</script>

