<template>
  <div class="countDown " v-if="info.countdown && countdownFlag">
    
    <span>{{ d }}</span
    ><span>{{ h }}</span
    >:<span>{{ m }}</span
    >:<span>{{ s }}</span>
  </div>
</template>

<script>
export default {
  name: "countDown",
  props: {
    info: {
      type: Object,
      // 
      default: () => []
    }
  },
  data() {
    return {
      d: 0,
      h: 0,
      m: 0,
      s: 0,
      detail: {},
      countdownFlag: true
    };
  },
  created() {
    setInterval(this.getCountDown, 1000);
    if (this.d == 0 && this.h == 0 && this.m == 0 && this.s == 0) {
      clearInterval(this.getCountDown);
      this.countdownFlag = false;
    }
  },
  methods: {
    getCountDown() {
      //
      let newTime = new Date();
      //
      let endTime = new Date(this.info.countdown);
      //
      let gap = endTime.getTime() - newTime.getTime();
      //
      if (gap > 0) {
        /*10001000,1 6060602424*/
        this.d = Math.floor(gap / 1000 / 60 / 60 / 24);
        this.h = Math.floor((gap / 1000 / 60 / 60) % 24);
        this.m = Math.floor((gap / 1000 / 60) % 60);
        this.s = Math.floor((gap / 1000) % 60);
        this.countdownFlag = true;
      } else {
        this.countdownFlag = false;
      }
    }
  },
  watch: {
    // info() {
    //   this.detail = this.info;
    //   console.log(this.detail);
    // }
  }
};
</script>

